@import url(https://fonts.googleapis.com/css2?family=Boogaloo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Boogaloo&family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@200;300;400;500;600;700&display=swap);
:root {
    --color1: #060099;
    --color2: #C51E52;
    --color3: #012515;
    --color4: #427a8b;
    --color5: #8E9AAF;
    --color6: linear-gradient(to bottom, #ff5722 0%, #ff6699 100%);
    --color7: linear-gradient(to bottom, #673ab7 0%, #cc00ff 100%);
    --white: #FFFFFF;
    --black: #000000;
    --font1: 'Boogaloo', sans-serif;
    --font2: 'Poppins', sans-serif;
}
.airdroppage {
    background: #2b2d2f;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: 250px;
}
.airdroppage h1 {
    font: 2.5rem/4rem 'Boogaloo', sans-serif;
    font: 2.5rem/4rem var(--font1);
    color: #FFFFFF;
    color: var(--white);
    font-weight: bold;
}
.airdroppage h2 {
    font: 1.125rem/1.8rem 'Poppins', sans-serif;
    font: 1.125rem/1.8rem var(--font2);
    color: #FFFFFF;
    color: var(--white);
    font-weight: bold;
    margin-bottom: 2rem;
}

@media all and (min-width: 0px) {
    .stake-app {
        background: #2b2d2f;
        padding-top: 100px;
        align-items: center;
        text-align: center;
        margin-left: 250px;
    }
    /* .connect-btn{
        background: var(--white);
        font: 1.125rem/1.6rem var(--font1);
        color: var(--color1);
        border-radius: 12px;
        border: none;
        height: 50px;
        width: 136px;
        margin-bottom: 2rem;
    } */
    .stake-app h1 {
        font: 2.5rem/4rem 'Boogaloo', sans-serif;
        font: 2.5rem/4rem var(--font1);
        color: #FFFFFF;
        color: var(--white);
        font-weight: bold;
    }
    .stake-app h2 {
        font: 1.125rem/1.8rem 'Poppins', sans-serif;
        font: 1.125rem/1.8rem var(--font2);
        color: #FFFFFF;
        color: var(--white);
        font-weight: bold;
        margin-bottom: 2rem;
    }
    .card-container {
        display: inline-block;
        flex-direction: column;
        justify-content: space-evenly;
        width: 28%;
        margin: 30px 2%;     
        padding: 20px 30px;   
        border-radius: 20px;
        transition: 0.5s all;
        background: #000000;
        background: var(--black);
    }
    .card-container .titles h2 {        
        font-size: 23px;
        display: block;
        margin-bottom: 0px;
    }
    .card-container blockquote {        
        font-size: 28px;
        display: block;
        color: #ffffff;
    }
    .card-container:hover {
        -webkit-transform: scale(1.005);
                transform: scale(1.005);
        transition: 0.5s all;
    }
    .btn {
        background: #20852d;
        color: #FFFFFF;
        color: var(--white);
        font: 1.5rem/1.5rem 'Boogaloo', sans-serif;
        font: 1.5rem/1.5rem var(--font1);
        border-radius: 12px;
        font-weight: bold;
        outline: none;
        border: none;
        height: 50px;
        width: 180px;
        margin: auto;
        cursor: pointer;
    }
    .mb-5 {
        margin: 1rem 0;
    }
    .form {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
    }
    .form input {
        height: 56px;
        max-width: 40%;
        width: 50%;
        outline: none;
        border: none;
        border-radius: 12px 0 0 12px;
        font: 1.125rem/1.8rem 'Poppins', sans-serif;
        font: 1.125rem/1.8rem var(--font2);
        color: #000000;
        color: var(--black);
        text-align: center;
    }
    .form button {
        margin: 1rem 0;
    }
    .form input::-webkit-input-placeholder {
        padding: 0 0.5rem;
        font: 0.8rem/1rem 'Poppins', sans-serif;
        font: 0.8rem/1rem var(--font2);
        color: #000000;
        color: var(--black)
    }
    .form input:-ms-input-placeholder {
        padding: 0 0.5rem;
        font: 0.8rem/1rem 'Poppins', sans-serif;
        font: 0.8rem/1rem var(--font2);
        color: #000000;
        color: var(--black)
    }
    .form input::placeholder,
    .form input:focus {
        padding: 0 0.5rem;
        font: 0.8rem/1rem 'Poppins', sans-serif;
        font: 0.8rem/1rem var(--font2);
        color: #000000;
        color: var(--black)
    }
    .reward {
        background: #8E9AAF;
        background: var(--color5);
        border: 1px solid #FFFFFF;
        border: 1px solid var(--white);
        font: 0.8rem/1rem 'Poppins', sans-serif;
        font: 0.8rem/1rem var(--font2);
        font-weight: 700;
        border-radius: 12px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 50px;
        width: 90%;
        margin: 0 auto 5px;
        color: #FFFFFF;
        color: var(--white);
    }
    .reward span {
        padding: 0 0.5rem 0 0;
    }
    .stake-app p {
        margin: 0.5rem 0 1.5rem;
        font: 1rem/1.6rem 'Poppins', sans-serif;
        font: 1rem/1.6rem var(--font2);
        color: green;
        font-weight: 500;
    }
    .stake {
        color: #0000;
    }
    .airdropitem {
        height: 300px;
        width: 500px;
    }
    .airdropitem blockquote {
        background: #000000;
        background: var(--black);
        border: none;
        margin-bottom: 50px;
        font-size: 20px;
    }
    .connect-btn {
        background: none;
        color: #ffffff;
        border: none;
        font-size: 18px;
        position: absolute;
        right: 50px;
        top: 35px;
        cursor: pointer;
    }
}

@media all and (min-width: 768px) {
    .form input {
        width: 55%;
    }
}
@media all and (min-width: 0px) {
    .nav-wrap {
        background: #000000;        
        padding: 25px;
        left: 0;
        right: auto;
        top: 0;
        flex: 1 0 auto;
        width: 250px;
        height: 100%;
        display: inline;
        display: initial;
        outline: 0;        
        z-index: 7;
        border-right: 1px solid grey;
        position: fixed;
    }
    .nav-wrap a {
        color: #fff;
        font-size: 20px;
        text-decoration: none;
        display: inline-block;
        width: 100%;
        margin-top: 25px;
    }
    .nav-wrap .navbarlinkimgs, svg {
        width: 25px;
        margin-right: 15px;
        vertical-align: text-top;
    }
    .nav-wrap ul {
        display: flex;
        justify-content: center;
    }
    .nav-wrap ul li {
        margin: 0 1rem;
    }
    .nav-wrap ul li a {
        color: var(--white);
        font: 2.5rem/4rem var(--font1);
        text-decoration: none;
    }
    .mintbox {
        background: linear-gradient(to bottom, #ff5722 0%, #ff6699 100%);
        border-radius: 20px;
        width: 100px;
        text-align: center;
    }
    .stakebox {
        background: linear-gradient(to bottom, #ff5722 0%, #ff6699 100%);
        border-radius: 20px;
        width: 100px;
        text-align: center;
    }
    .logo {
        /* background: white; */
        width: 63px;
        height: 60px;
        margin-right: 600px;
    }
}

